<script>
    import {term, category, showResults} from './stores.js'
    // import {Collapse} from 'sveltestrap'


    // let isOpen = false
    let val = ''
   // $:category.set('Boiled') Select food state!

    // $:term.set(val) //MAKE IT LIVE RESPONSE ON INPUT


    ////ON MOUSE DOWN IN SEARCH
    const onKeyPress = e => {
    if (e.charCode === 13) {
        term.set(val.toUpperCase())
        showResults.set(true)
    };
  };

</script>

        
    <div class="col-7">
        <input class="form form-control" 
        style="text-transform: uppercase;"
        on:keypress={onKeyPress}
        type="text" bind:value="{val}">
        </div>

        <div class="col-3">
        <button class="btn btn-primary" style="margin-left: 12px;"
        on:click="{()=>{
            // console.log(val.toUpperCase())
            term.set(val.toUpperCase())
            showResults.set(true)
        
        }}"
        >SEARCH</button>
                </div>




